/*
* Style Works for both Editor and Frontend
*/
.wp-block-aab-accordion-item {
  overflow: hidden;
  transition: all 0.3s linear;


  .aagb__accordion_head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    outline: 0;

    &.hasSubHeading {
      &.active {
        .head_content_wrapper {
          transform: translateY(12px);
        }

        .aagb__accordion_subheading {
          opacity: 0;
          visibility: hidden;
        }
      }
    }

    .head_content_wrapper {
      transition: all 0.3s ease-in-out;
    }

    .heading-icon {
      margin-right: 18px;

      img {
        max-width: 50px;
        height: auto;
        box-shadow: none;
      }
    }


    // Step css
    .complete-sign {
      background-color: #66CDA3;
      color: #fff;
      border-radius: 50%;
      text-align: center;
      font-size: 15px;
      display: none;
      padding: 0 5px;
      line-height: 1.6;
    }

    // header icon
    .aagb__accordion_icon {
      width: 20px;
      height: 20px;
      border-radius: 50%;

      // Steps
      .aagb__icon_dashicons_box {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
      }


    }

    // header icon position
    &.aagb_right_icon {
      flex-direction: row;
    }

    &.aagb_left_icon {
      flex-direction: row-reverse;
    }

    // heading
    .aagb__accordion_heading {
      display: flex;
      flex: 1;
      align-items: center;
      transition: all 0.3s ease-in-out;

      .aagb__accordion_subheading {
        transition: all 0.2s ease-in-out;
      }

      &.aagb_right_icon {
        margin-right: 20px;
        flex: 1;
      }

      &.aagb_left_icon {
        margin-left: 20px;
        flex: 1;
      }

      //&.aagb_right_link {
      //  display: flex;
      //  align-items: center;
      //  flex: 1;
      //}

      &.aagb_left_link {
        .title_wrapper {
          flex-direction: row-reverse;
        }
      }

      .title_wrapper {
        position: relative;
        display: flex;
        align-items: center;
        gap: 10px;
      }

      .aagb__accordion_title.line-through {
        text-decoration: line-through;
      }

      .aagb__accordion_title {
        padding-top: 0;
        padding-bottom: 0;
      }


      a {
        text-decoration: none;
        color: #333333;
        opacity: 0.5;
        font-size: 1.4rem !important;
        line-height: 0.5;
        padding-top: 0px;

        &.anchorjs-link {
          &:after {
            font-size: 1.4rem !important;
          }
        }
      }

      &:hover {
        a {
          opacity: 1;
        }
      }
    }
  }

  .collapse-icon span {
    display: block;
  }

  .aagb__accordion_body {
    display: none;
    padding-right: 10px;
    transition: border-top-color 0.3s linear;
    // Step css
    .continue {
      background: #41a9f8;
      border-radius: 3px;
      color: #ffffff;
      padding: 10px 20px;
      cursor: pointer;
      display: flex;
      justify-content: space-between;


      .step-down-icon {
        color: #41a9f8;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }


    .aagb__accordion_component {
      position: relative;

      p {
        margin-bottom: 20px;
      }

      p:last-child {
        margin-bottom: 10px;
      }

      .aagb_overlay {
        content: '';
        position: absolute;
        background: linear-gradient(to bottom, #ffffff00, rgba(#ffffff, 0.9));
        height: 50px;
        right: 0;
        bottom: -5px;
        left: 0;
        width: 100%;
        z-index: 2;
      }
    }
  }
}

.wp-block-aab-accordion-item.aagb__accordion_active {
  overflow: hidden;

}

.aagb_form_inner {
  max-width: 600px;
  width: 100%;
  margin: 0 auto 40px;
  text-align: center;

  .aagb_form_group {
    position: relative;

    &:before {
      position: absolute;
      right: 14px;
      content: "\f179";
      font-family: dashicons;
      top: 11px;
      font-size: 26px;
      color: #4444;
    }
  }

  .aagb_form_control {
    width: 100%;
    border: 1px solid #ccc;
    padding: 0 30px;
    border-radius: 4px;
    height: 60px;
    font-size: 18px;
    color: #333;
    padding-right: 55px;

    &::placeholder {
      color: #848d95 !important;
    }
  }


  input[type=search]::-webkit-search-cancel-button {
    -webkit-appearance: searchfield-cancel-button !important;
    height: 18px;
    width: 18px;
    margin-left: .4em;
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='%23777'><path d='M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z'/></svg>");
    cursor: pointer;
  }

  .help-block {
    font-size: 16px;
  }
}


.aagb_accordion_wrapper_btn {
  display: flex;
  justify-content: flex-end;

  a {
    margin: 0px 0 5px 16px;
    color: #333;
    font-weight: 500;
    font-size: 16px;
    text-decoration: none !important;
    display: flex;

    &:hover {
      color: #069;
    }

    svg {
      width: 0.875em;
      margin-right: 4px;
    }
  }
}

.aagb__accordion_body .aagb_button_toggle {
  padding: 0px;
  border: 0px;
  color: #333;
  font-size: 16px;
  font-weight: 500;
  background: transparent;
  border: 0px;

  &:hover {
    color: #4c4cf1;
    text-decoration: underline;
  }

  &:focus {
    outline: none;
    box-shadow: none;
  }
}

.label-d-block {
  display: block;
  margin-bottom: 5px;
}


// Step CSS
.wp-block-aab-group-accordion {
  .aagb__accordion_container.step {
    .aagb__accordion_icon {
      width: auto;
      height: auto;
    }
  }

  .aagb__accordion_container.style-qa.step {
    .aagb__accordion_body {
      .continue {
        width: 90%;
        margin-bottom: 10px;
      }
    }
  }
}

