.wp-block-aab-group-accordion {

  .step-result {
    border: 1px solid #3e94b2;
    display: none;
    padding: 20px;
  }
}


//Group Accodion QA style
.wp-block-aab-accordion-item.aagb__accordion_container.style-qa {

  .aagb__accordion_head {
    padding: 10px;

    .aagb__accordion_heading {
      gap: 10px;


      .icon-container {
        display: block;
        position: relative;


        .icon-q {
          background: #505050;
          color: #fff;
          width: 80px;
          line-height: 43px;
          font-size: 25px;
          text-align: center;
          border-radius: 3px;
          font-weight: 500;
          position: relative;
          z-index: 2;
        }

        .icon-a {
          background: #f5a623;
          color: #fff;
          width: 80px;
          line-height: 43px;
          font-size: 25px;
          text-align: center;
          border-radius: 3px;
          font-weight: 500;
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;
          transition: all 0.4s ease-in-out;
          z-index: 1;
        }
      }
    }
  }

  .aagb__accordion_body {
    //padding: 0 0 0 90px;
    border-top: none !important;

    .block-editor-inner-blocks {
      .block-editor-block-list__layout {
        :first-child {
          margin-top: 0;
        }
      }
    }
  }

  .aagb__accordion_body.aagb__accordion_body--show {
    border-top: none !important;
    transition: border-top-color 0.3s linear;

    .aagb__accordion_component :first-child {
      margin-top: 0;
    }
  }

  :where(.wp-site-blocks *:focus) {
    outline-width: 0;
    outline-style: none;
  }

}

.wp-block-aab-accordion-item.aagb__accordion_container.style-qa.aagb__accordion_active {
  .aagb__accordion_head {
    :active {
      border-bottom: none !important;
    }

    .aagb__accordion_heading {
      .icon-container {
        .icon-a {
          top: 43px;
          opacity: 1;
        }
      }
    }
  }


}


.block-editor-block-list__block.wp-block.aagb__accordion_container.style-qa.wp-block-aab-accordion-item {
  .aagb__accordion_heading {
    .icon-container {
      .icon-a {
        top: 40px;
        opacity: 1;
        z-index: 101;
      }
    }
  }
}

//  check-list
.wp-block-aab-accordion-item.aagb__accordion_container.check-list {
  .aagb__accordion_head {
    background: red;

    .aagb__accordion_heading {
      //.checklist-box{
      //
      //}

      /* Basic Styling */
      .checklist-box {
        width: 25px;
        height: 25px;
        -moz-appearance: none;
        appearance: none;
        -webkit-appearance: none;
        /* align-self: start; */
        border: 1px solid #e6e6e6;
        background-color: #fff;
        cursor: pointer;
        margin-right: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 4px;
        transition: transform 0.3s ease-in-out;
      }

      /* Checked State */
      .checklist-box:checked {
        //background-color: #f5a623; /* Color when checked */
      }

      /* Optional: Checkmark Icon (using pseudo-elements) */
      .checklist-box:checked::after {
        content: "✓";
        display: block;
        text-align: center;
        font-size: 16px; /* Adjust size as needed */
        color: #0707a3;
      }

      /* Optional: Focus State */
      .checklist-box:focus {
        outline: none; /* Remove default focus outline */
        //box-shadow: 0 0 3px 2px #f5a623; /* Add a subtle box shadow */
      }
    }
  }
}


